import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import './sample-page.scss';

import { Section, Main, PageHeader, PageHeaderTitle } from '@redhat-cloud-services/frontend-components';

/**
 * A smart component that handles all the api calls and data needed by the dumb components.
 * Smart components are usually classes.
 *
 * https://reactjs.org/docs/components-and-props.html
 * https://medium.com/@thejasonfile/dumb-components-and-smart-components-e7b33a698d43
 */
class SamplePage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: undefined
        };
        insights.chrome.auth.getUser().then(data => this.setState({ user: data }));
    }

    render() {
        if (this.state.user === undefined) {
            return (<p>Who are you?</p>);
        }

        let permissions = [];
        Object.keys(this.state.user.entitlements).map(key => {
            let stuff = this.state.user.entitlements[key];
            if (stuff.is_entitled) {
                permissions.push(<p key={ key }>You have access to {key}</p>);
            } else {
                permissions.push(<p key={ key }>Sadly you don&apos;t have access to {key}</p>);
            }
        });
        return (
            <React.Fragment>
                <PageHeader>
                    <PageHeaderTitle title='Who are you?' />
                    <p>Hai, you are {this.state.user.identity.user.first_name} {this.state.user.identity.user.last_name}</p>
                </PageHeader>
                <Main>
                    <Section>
                        { permissions }
                    </Section>
                </Main>
            </React.Fragment>
        );
    }
}

export default withRouter(SamplePage);
